  /* this is where i add all the css */
    * {
      box-sizing: border-box;
      margin: 0;
      padding: 0;
    }
    
    .calculator-container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
      background-color: #f5f5f5;
    }
    
    .calculator {
      width: 350px;
      padding: 20px;
      border-radius: 20px;
      background-color: #333;
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
    }
    
    .calculator-display {
      width: 100%;
      height: 60px;
      background-color: #222;
      color: #fff;
      text-align: right;
      padding: 10px;
      font-size: 2rem;
      margin-bottom: 20px;
      border: none;
      border-radius: 10px;
    }
    
    .calculator-buttons {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 10px;
    }
    
    button {
      background-color: #444;
      color: #fff;
      border: none;
      border-radius: 10px;
      padding: 20px;
      font-size: 1.5rem;
      cursor: pointer;
      transition: background-color 0.2s;
    }
    
    button:active {
      background-color: #666;
    }
    
    button.zero {
      grid-column: span 2;
    }
    
    @media (max-width: 678px) {
      .calculator-container {
        height: 100%;
        padding: 20px; 
      }
    
      .calculator {
        width: 100%;
        height: 100%;
        padding: 10px;
      }
    
      .calculator-display {
        height: 50px;
        font-size: 1.5rem;
      }
    
      button {
        padding: 15px;
        font-size: 1.2rem;
      }
    }